.message {
    color: rgb(235, 241, 241);
    font-size: 16px;
    background-color: rgb(244, 183, 71);
    border: 2px solid black;
}

.delete-button {
    button {
        border-radius: 100%;
        border: 0.5em solid #272727;
        background-color: #9DA3A4;
        color: #272727;
        transition: 500ms linear;
        box-shadow: rgba(0, 0, 0, 0.25) 0px 54px 55px, rgba(0, 0, 0, 0.12) 0px -12px 30px, rgba(0, 0, 0, 0.12) 0px 4px 6px, rgba(0, 0, 0, 0.17) 0px 12px 13px, rgba(0, 0, 0, 0.09) 0px -3px 5px;
    }

    .complete-button {
        button {
            border-radius: 100%;
            border: 0.5em solid #272727;
            background-color: #9DA3A4;
            color: #272727;
            transition: 500ms linear;
            box-shadow: rgba(0, 0, 0, 0.25) 0px 54px 55px, rgba(0, 0, 0, 0.12) 0px -12px 30px, rgba(0, 0, 0, 0.12) 0px 4px 6px, rgba(0, 0, 0, 0.17) 0px 12px 13px, rgba(0, 0, 0, 0.09) 0px -3px 5px;
        }

        button:hover {
            transform: scale(1.1);
        }
    }

    button:hover {
        transform: scale(1.1);
    }
}